.gridContainer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 20px;
}

@media (max-width: 768px) {
  .gridContainer {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: auto;
  }
}

.gridItem {
  display: flex;
  justify-content: center;
}